<script>
import BibleSchoolModal from "../components/BibleSchoolModal";
import { mapGetters } from "vuex";
export default {
  props: [
    "bgImage",
    "mainText",
    "subText",
    "buttonText",
    "screen",
    "events",
    "color",
    "position",
    "button",
    "donations",
    "subTextClass"
  ],
  data() {
    return {
      check: {
        visibility: "hidden"
      }
    };
  },
  components: {
    BibleSchoolModal
  },
  computed: {
    ...mapGetters(["getAllEvents"]),
    allEvents() {
      return this.getAllEvents;
    }
  },

  methods: {
    next(x) {
      this.$router.push({
        name: "MainTheme",
        params: {
          theme: x
        }
      });
    },
    facebook() {
      window.open("https://web.facebook.com/groups/368974059816565", "_blank");
    },
    twitter() {
      window.open("https://twitter.com/MissionsCare", "_blank");
    },
    youtube() {
      window.open(
        "https://www.youtube.com/channel/UCPe9rUAnO_P7AFsswV7E2Qw",
        "_blank"
      );
    },
    modalShow() {
      if (this.screen === "testimonies") {
        this.$emit("open");
      }
      if (this.screen === "Home") {
        this.$router.push("/about-us");
      }
    }
  }
};
</script>

